import React, { useState, useEffect } from "react";

import banner_1 from "assets/img/home-1-slider-image-1.jpg";
import banner_2 from "assets/img/home-1-slider-image-2.jpg";
import banner_3 from "assets/img/home-1-slider-image-3.jpg";

function HomeComponent() {
   const [banner, setBanner] = useState(0);
   const [bannerExample] = useState([
      { url: banner_1, name: "banner_1" },
      { url: banner_2, name: "banner_2" },
      { url: banner_3, name: "banner_3" }
   ]);

   const bannerChange = () => {
      return (
         <img
            className="banner-full"
            src={bannerExample[banner].url}
            alt={bannerExample[banner].name}
         />
      );
   };

   useEffect(() => {
      console.log(window.screen.height)   
      const interval = setInterval(() => {
         const bannerLength = bannerExample.length - 1;
         setBanner(i => i < bannerLength ? i + 1 : i = 0);
      }, 4000);

      return () => {
         clearInterval(interval);
      };
   }, [bannerExample]);

   return <div className="banner">{bannerChange()}</div>;
}

export default HomeComponent;
